// ECS Feed

.ecs-event-list {
    padding: $margin;
    background: $white;
    padding: $margin !important;
    border-radius: 3px !important;
    box-shadow: 0 1px 3px 0 rgba(53, 69, 88, 0.2);
    list-style: none;

    .ecs-event {
        margin-bottom: $margin;
        padding-bottom: $margin;
        border-bottom: 1px solid $border-color;
        list-style: none;
    }

    .entry-title {
        margin-bottom: 0

    }
}
