// Product Grid

.products {
	display: flex;
	flex-flow: row wrap;
	padding: 0 $margin * 0.5;

	.entry--type-product {
		margin: $margin * 0.5;
		//padding-bottom: 30px;
		flex: 0 1 100%;
		position: relative;

		@include mq(tablet-portrait-up) {
			flex-basis: calc(50% - #{$margin});
			width: calc(50% - #{$margin});
		}

		@include mq(desktop-up) {
			flex-basis: calc(33.33333334% - #{$margin});
			width: calc(33.33333334% - #{$margin});
		}

		@include mq(big-desktop-up) {
			flex-basis: calc(25% - #{$margin});
			width: calc(25% - #{$margin});
		}

		@include mq(super-desktop-up) {
			flex-basis: calc(20% - #{$margin});
			width: calc(20% - #{$margin});
		}

		@include mq(super-desktop-up) {
			//flex-basis: calc(16.66667% - #{$margin});
			//width: calc(16.66667% - #{$margin});
		}

		&.entry--type-tribe_events {
			//flex-basis: 100% !important;
			//width: 100%;
		}
	}
}

// Grid for Beaver Builder WooCommerce module
.woocommerce {
	&.columns {
		&-3 {
			
			.entry--type-product {
				
				@include mq(tablet-landscape-up) {
					flex-basis: calc(33.33333334% - #{$margin});
					width: calc(33.33333334% - #{$margin});
				}
		
				@include mq(desktop-up) {
					flex-basis: calc(33.33333334% - #{$margin});
					width: calc(33.33333334% - #{$margin});
				}
		
				@include mq(big-desktop-up) {
					flex-basis: calc(33.33333334% - #{$margin});
					width: calc(33.33333334% - #{$margin});
				}
		
				@include mq(super-desktop-up) {
					flex-basis: calc(33.33333334% - #{$margin});
					width: calc(33.33333334% - #{$margin});
				}
		
				
			}
		}
	}
}